import datas from './bdd'

class Restaurant {
    constructor(id, name, image, note, drive_time) {
        this.id = id
        this.name = name
        this.image = image
        this.note = note
        this.drive_time = drive_time
    }
}

var all_restaurant = []

for (const restaurant of datas) {
          
    const rest = new Restaurant(restaurant.id ,restaurant.name, restaurant.image, restaurant.note, restaurant.drive_time)
    all_restaurant.push(rest)

}
export default all_restaurant