<template>

    <div class="restaurant" v-for="(restaurant, i) in restaurants_filter" :key="i">

        <h1> {{ restaurant.name }} </h1>

        <img :src="image" alt="" class="restaurant--image">
        
        <div class="restaurant--information">

            <div class="top">
                <div class="note"> {{ restaurant.note }} </div>
            </div>

            <div class="time">
            {{ restaurant.drive_time }}
            </div>

        </div>
        <div class="description--restaurant">
            <p>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Et quisquam ex, cumque accusamus suscipit numquam iste nobis illo sed ipsum. Hic odit repellendus harum velit placeat dignissimos labore, voluptatibus voluptas.
                Debitis suscipit id accusantium quasi aliquam magnam culpa asperiores praesentium. Maxime quas illo labore, placeat voluptates, sapiente quaerat maiores vel quis voluptate commodi rem ipsum perspiciatis fuga, nobis quia excepturi.
                Hic nam maiores corporis. Distinctio, maiores incidunt temporibus facere veritatis rerum. Nihil repellat ratione eius quisquam consequatur dolore explicabo iure. Ea unde molestiae aliquid consequuntur debitis eveniet explicabo nulla magni!
            </p>
        </div>
    </div>

</template>

<script>
// Import
import { ref } from 'vue'
import datas from '@/bdd/export_bdd.js'
import image from "@/assets/Featuredegoussi.png"

export default {
    name: "RestaurantPage",
    data() {

      let restaurants_filter = ref([]);
      let regex = RegExp(this.$route.params.name);
      restaurants_filter.value = datas.filter(restaurant => regex.test(restaurant.name));

      this.$watch(
          () => this.$route.params, 
          (newValue) => {
            let regex = RegExp(newValue.name);
            restaurants_filter.value = datas.filter(restaurant => regex.test(restaurant.name));
          } 
      )

      return {
          restaurants_filter,
          image
      }
    }
}
</script>

<style lang="scss">

.restaurant {
  min-width: calc(100% / 3.2);

  .restaurant--image {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 60vh;
    width: 100%;
  }

  .restaurant--information {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em;

    .top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      min-height: 5%;

      .note {
        background: rgba(0,0,0,0.1);
        border-radius: 50%;
        color: rgba(0,0,0,.5);
        font-size: small;
        padding: 5px;
      }

    }

    .time {
      color: rgba(0,0,0,.7);
      font-size: small;
    }
    .description--restaurant {

        p {
            text-align: justify;
        }

    }
  }
}

</style>