<template>
  <button @click="Clique">Cliquer</button>
  <Titre :titre="statut"/>
</template>

<script>
import Titre from './Titre.vue';

export default {
  name:'Essai',
  data: () => {
    let statut = "";
    return {
      statut,
      i: 0
    }
  },
  methods: {
    Clique() {
      this.i++;
      if(this.i<=50) {
          this.statut = "Vous m'avez appuyé "+this.i+" fois.";
      }
      else {
          this.statut = "Je suis fatigué. Vous m'avez trop appuyé.";
      }
    }
  },
  components: {
    Titre
  }

}
</script>

