<template>

  <div class="restaurant--card">

    <router-link :to="{ name: 'restaurant', params: { name: info_restaurant.name } }">
      <div :style="{ changeBackground }" class="restaurant--image">
      
      </div>
    </router-link>
    <div v-if="etat" class="restaurant--like" @click="changeEtat">
      <img src="@/assets/coeur.svg" alt="">
    </div>
    <div v-else class="restaurant--like" @click="changeEtat">
      <img src="@/assets/coeur2.svg" alt="">
    </div>
    <div class="restaurant--information">

        <div class="top" >
          <h3>{{ info_restaurant.name }}</h3>
          <div class="note"> {{ info_restaurant.note }} </div>
        </div>

        <div class="bottom">
          <div class="time" @click="essai">
            {{ info_restaurant.drive_time }}
          </div>
          <div class="add--panier" @click="obtainInfo">
            <img src="@/assets/cart-plus.svg" alt="">
            Ajouter
          </div>
        </div>
    </div>

  </div>

</template>

<script>
// Import
import { computed, ref } from 'vue'

export default {
  name: "RestaurantCard",
  props: {
    info_restaurant: Object
  },
  
  data(props) {
    
    return {
      white: {
        background: "white"
      },
      red: {
        background: "#ff0007"
      },
      font: {
        color: '#14f888',
        fontSize: '20px'
      },
      etat: true,
      restaurant: props.info_restaurant,

    }
  },
  methods: {
    changeEtat: function() {
      if(!this.etat) {
        this.etat = true
      }
      else {
        this.etat = false
      }
      console.log(this.etat)
    },

    essai: ()=> {
      console.log(this.restaurant)
    }

    
  }, 
  setup(props) {
    let all_restaurant_add = ref([])
    const changeBackground = computed(()=> {
      return {
        backgroundImage: `url(${props.info_restaurant.image})`
      }
    })

    const obtainInfo = ()=> {
      //console.log(this.props.info_restaurant)
      all_restaurant_add.value.push(props.info_restaurant)
    }

    // Retour
    return {
      changeBackground,
      obtainInfo,
      all_restaurant_add
    }
  }
  
}
</script>

<style lang="scss">

@import url(@/styles/style.css);

.restaurant--card {
  min-width: calc(100% / 3.2);
  height: 38vh;
  position: relative;

  .restaurant--image {
    background-image: url(@/assets/hamburger2.jpeg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 80%;
  }

  .restaurant--like {
    position: absolute;
    padding: 3px;
    top: 0%;
    right: 0;
    cursor: pointer;
    
    &.white {
      background: white;
    }
    &.red {
      background: #ff0007;
    }
  }

  .restaurant--information {

    .top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      min-height: 5%;

      .note {
        background: rgba(0,0,0,0.1);
        border-radius: 50%;
        color: rgba(0,0,0,.5);
        font-size: small;
        padding: 5px;
      }

    }

    .bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;

      
      .time {
        color: rgba(0,0,0,.7);
        font-size: small;
      }

      .add--panier {
        display: flex;
        align-items: center;
        cursor: pointer;
        border: 1px solid #0dd63f;
        border-radius: 1em;
        padding: 0.5em 1.5em;
        background-color: #0dd63f;
        color: white;
        font-size: 1em;
        transition: transform 1s;

        &:active {
          transform: scale(1.15);
        }

        img {
          width: 20px;
          height: 20px;
          padding-right: 10px;
        }

      }
    }
  }
}

@media screen and (max-width: 770px) {
 .restaurant--card {
  min-width: 100%;
  margin-bottom: 5em;
 } 
}
</style>