<template>
  <div class="header">
    <router-link to="/">
      <img src="@/assets/Uber-Eats-Logo.png" alt="Logo Ubereat">
    </router-link>
    <div :class="classes">
      <router-link to="/" class="nav-link" @click="open">
        Accueil
      </router-link>
      <router-link to="/panier" class="nav-link" @click="open">
        Panier
      </router-link>
      <input v-model="user_search" type="text" name="search" id="search" placeholder="De quoi avez vous besoin ?">
      <div class="search">
        <router-link :to="{ name: 'restaurant', params: { name: restaurant.name } }" class="link"
          v-for="(restaurant, i) in search_restaurant" :key="i">
          <div class="container--restaurant--search" @click="removeList">
            <div class="container--image">
              <img src="@/assets/hamburger1.jpeg" alt="">
            </div>
            <p>{{ restaurant.name }}</p>
          </div>
        </router-link>
      </div>
    </div>
    <MenuComponent class="menu" @click="open" :etat="statut" />

  </div>
</template>

<script>
// Import
import { ref, watch } from 'vue'
import datas from '../bdd/export_bdd'
import MenuComponent from './MenuComponent.vue'

export default {
  name: "HeaderComponent",
  setup() {

    let info1 = "input-container"
    let info2 = "active"
    let classes = ref([])
    let i = 0
    let statut = ref("1")
    classes.value.push(info1)

    const open = () => {
      i++

      if (i <= 1) {
        classes.value.push(info2)
        statut.value = "1"
      }
      else {
        classes.value.pop(info2)
        i = 0
        statut.value = "0"
      }
      //console.log(statut.value)
    }

    // User Search Restaurant
    let user_search = ref("");
    let search_restaurant = ref([]);
    const removeList = function () {
      search_restaurant.value = [];
      user_search.value = "";
    };
    watch(user_search, newValue => {
      let regex = RegExp(newValue);
      let all_restaurant_filter = datas.filter(restaurant => regex.test(restaurant.name));
      newValue == 0 ? search_restaurant.value = [] : search_restaurant.value = all_restaurant_filter;
    });
    // Retour
    return {
      user_search,
      search_restaurant,
      removeList,
      classes,
      open,
      statut
    };
  },
  components: { MenuComponent }
}
</script>

<style lang="scss">
.header {
  height: 120px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;

  img {
    width: 200px;
  }

  .input-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    .nav-link {
      text-decoration: none;
      margin-right: 1em;
      border: 1px solid #f6f6f6;
      padding: 10px 20px;
      color: inherit;
      transition: all 0.6s;

      &:hover {
        transform: scale(1.2);
        background: #f6f6f6;
      }
    }

    input {
      background-color: #f6f6f6;
      border: none;
      border-radius: 5px;
      height: 60px;
      width: 25em;
      outline: 0;
      padding-left: 20px;
    }

    .search {
      position: absolute;
      top: 100%;
      width: 100%;
      background-color: #fff;
      overflow-y: scroll;
      z-index: 1;

      .link {
        color: inherit;
        text-decoration: none;
      }

      .container--restaurant--search {
        display: flex;
        align-items: center;
        padding: 10px;

        &:hover {
          background: #f6f6f6;
        }

        .container--image {
          width: 60px;
          height: 60px;
          margin-right: 20px;

          img {
            width: auto;
            height: 100%;
            border-radius: 50%;
          }
        }
      }
    }
  }

  .menu {
    display: none;
  }
}

@media screen and (max-width: 770px) {

  .header {
    position: sticky;
    top: 0;
    background: #fff;
    z-index: 2;

    img {
      width: 140px;
    }

    .input-container.active {
      flex-direction: column;
      justify-content: center;
      z-index: 2;
      position: fixed;
      width: 100%;
      height: 100vh;
      top: 0;
      background: white;
      display: flex;

      .nav-link,
      input {
        margin: 20px;
      }

      font-size: x-small;

      input {
        width: 15em;
        height: 50px;
      }

      .search {
        top: 65.5%;
        width: 48.5%;
        height: 25%;
      }

    }

    .input-container {
      display: none;
    }

    .menu {
      display: block;
      z-index: 3;
    }
  }

}
</style>