<template>
  <div class="container">
    <div :class="nosclasses" @click="displayMenu2">
      <div class="bar"></div>
      <div class="bar"></div>
      <div class="bar"></div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
export default {
  name: "MenuComponent",
  props: {
    etat: String
  },
  setup(props) {
    let info1 = "menu--container"
    let info2 = "active"
    let nosclasses = ref([])
    nosclasses.value.push(info1)
    let i = 0

    const displayMenu2 = () => {
      i++
      console.log("Appuie displayMenu2 : "+i)

      // if(this.statut=="1") {
      //   this.nosclasses.push(this.info2)
      // }
      // else {
      //   this.nosclasses= [this.info1]
      // }

      // console.log(nosclasses)

      let statut1, statut2 = ""
      statut1 = props.etat
      console.log("Statut 1 : ",statut1,"Statut 2 : ", statut2)

      if(statut2==statut1) {
        statut1 = 0
      }
      
      statut2 = statut1

      if (statut1==1) {
        nosclasses.value.push(info2);
      }
      else if(statut1==0) {
        nosclasses.value = [info1]
      }

      // else {
      // nosclasses.value = [info1]
      // }
    }

    return {
      nosclasses,
      displayMenu2
    }
  },
  data: (props) => {
    let info1 = "menu--container"
    let info2 = "active"
    let nosclasses = ref([])
    nosclasses.value.push(info1)
    return {
      info1,
      info2,
      nosclasses,
      i: 0,
      statut: props.etat
    }
  },
  methods: {
    displayMenu() {
      this.i++
      console.log("Appuie "+this.i)

      // if(this.statut=="1") {
      //   this.nosclasses.push(this.info2)
      // }
      // else {
      //   this.nosclasses= [this.info1]
      // }

      console.log(this.statut)
      console.log(this.nosclasses)

      // console.log(this.statut)

      if (this.i <= 1) {
        this.nosclasses.push(this.info2)
      }
      else {
        this.nosclasses.pop(this.info2)
        this.i = 0
      }
    }
  }
}
</script>

<style lang="scss">
@import url(../styles/style.css);

.container {
  border: 1px solid rgba(0,0,0,0.2);
  border-radius: 0 8px 0 8px;
  padding: 10px;

  .menu--container {
    width: 8vw;
    height: 4.8vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    transition: all 1s;

    .bar {
      width: 95%;
      height: 0.3em;
      background: #122f;
      border-radius: 1em;
      transition: all 1s;
    }
  }

  .menu--container.active {
    transform: rotate(360deg);

    .bar:nth-child(1) {
      transform: rotate(38deg) translate(4px);
      transform-origin: left;
    }

    .bar:nth-child(2) {
      display: none;
    }

    .bar:nth-child(3) {
      transform: rotate(-38deg) translate(4px);
      transform-origin: left;
    }

  }
}
</style>