<template>
  <div class="home">
    <div class="banniere">
      <p>Bienvenu chez <span>Uber Eats</span></p>
    </div>
    <RestaurantRow v-for="(data, i) in data_restaurant" :key="i" :three_restaurant="data"/>
  </div>
</template>

<script>
// Import
import { onMounted, ref } from 'vue'
import datas from '../bdd/export_bdd'

// Component
export default {
    name: "HomePage",
    setup() {
        let data_restaurant = ref([]);
        const makeDataRestaurant = () => {
            let three_restaurant = [];
            for (const restaurant of datas) {
                if (three_restaurant.length == 2) {
                    three_restaurant.push(restaurant);
                    data_restaurant.value.push(three_restaurant);
                    three_restaurant = [];
                }
                else {
                    three_restaurant.push(restaurant);
                }
            }
        };
        onMounted(makeDataRestaurant);
        // Retour
        return {
            data_restaurant
        };
    }
}

</script>

<style lang="scss">

.home {

  .banniere {
    height: 70vh;
    width: 100%;
    background-image: url(@/assets/banniere.jpg);
    background-repeat: repeat;
    background-position: center;
    background-size: cover;
    z-index: 0;

    p {
      font-size: 3em;
      color: #f6f6f6;
      position: absolute;
      top: 55%;
      left: calc((100% / 4));

      span {
        font-weight: bold;
        font-style: italic;
      }
    }
  }
}

@media screen and (max-width: 770px) {
 .home {

    .banniere {
      p {
        font-size: 1.75em;
        left: calc(100% / 8);
      }
    }
 }
}
</style>